import  httpProvider  from "../utils/request";

export  function reactList(data?:object){
  return  httpProvider.request({
    method:'POST',
    url:'/reactList',
    data
  });
}

// 一进页面的请求
export  function getList(data:object){
  return  httpProvider.request({
    method:'GET',
    url:'/reactGetList',
    params:data
  });
}

// 点击开始考试的时候渲染的数据
export  function getExamList(data:object){
  return  httpProvider.request({
    method:'POST',
    url:'/getexamList',
    data
  });
}
//
// 点击交卷的时候提交的考试信息
export  function submitExam(data:object){
  return  httpProvider.request({
    method:'POST',
    url:'/submitExamLists',
    data
  });
}