import { log } from 'console';
import React, { Component } from 'react';
import { Button ,Result} from 'antd';
import './topic.less';
import {submitExam} from '../../api/index';
import Alerts from '../alert/alert';
import {withRouter} from 'react-router-dom';

interface Iprops{
  allDatas?:any,
  startTime?:any,
  examId?:any,
  count?:any,
  history?:any
}

interface IState{
  dan?:any,
  Myrefs?:any
}
class Topic extends Component<Iprops> {
  tabData=(data:any)=>{
    const arry:any=[];
    const student:any=localStorage.getItem('student');
    // console.log(student);
    
    data.forEach((item:any)=>{
      const obj={
        // student_id:JSON.parse(student).name,
        id:item.id,
        type:item.type,
        answer:null
      };
      arry.push(obj);
    });
    // console.log(arry);
    
    return arry;
  };
  state = {
    dan:'',
    arr:this.tabData(this.props.allDatas),
    InterVal:null,
    ending:''
  };
  Myrefs: any;
  render() {
    const {dan,arr,InterVal} = this.state;
    
    const {allDatas,startTime,count} = this.props;
    return (
      <div className='box'>
        <div className="boxList">
          {
            allDatas.map((item:any,index:number) => {
              return <div key={index} className="littleBox">
                <h2>{index + 1}.{item.topic_nav}({item.type})</h2>
                {
                  item.topic_img ? <img src={'http://192.168.5.104:7777/'+item.topic_img} alt="" className='img'/> : ''
                }
                
                <div>
                  {

                    this.getLists(item).map((ite:any,idx:number) => {
                      
                      return ite.indexOf('img')===-1?
                        <div key={idx} className="answer">
                          {
                            item.type === "单选题" ? <div>
                              <input type="radio" className='fill' data-key={ite+','+item.id} name={item.id} value={ite} onChange={(e) => {
                                const index=arr.findIndex((ito:any)=>ito.id===item.id);
                                arr[index].answer=ite;
                                console.log(arr);
                            
                              }} />{item[ite]}</div> : ''
                          }
                          {
                            item.type === "多选题" ? <div className='answers'><input type="checkbox" className='fill' data-key={ite+','+item.id} value={ite} onChange={(e) => {
                              const index=arr.findIndex((ito:any)=>ito.id===item.id);
                              if(arr[index].answer===null){
                                arr[index].answer=ite;
                              }else{
                                if(arr[index].answer.indexOf(ite)===-1){
                                  const arry=arr[index].answer.split(',');
                                  arry.push(ite);
                                  arr[index].answer=arry.join(',');
                                
                                }else{
                                  const arry=arr[index].answer.split(',');
                                  arry.splice(arry.indexOf(ite),1);
                                  arr[index].answer=arry.join(',');
                                }
                              }
                            
                            
                            }} />{item[ite]}</div> : ''
                          }
                          {
                            item.type === "判断题" ? <div><input type="radio" className='fill'  data-key={ite+','+item.id} name={"idx" + item.id}  value={ite} onChange={(e) => {
                              const index=arr.findIndex((ito:any)=>ito.id===item.id);
                              arr[index].answer=ite;
                              console.log(arr[index]);
                            
                            }}/>{item[ite]}</div> : ''
                          }
                          {
                            item.type === "简答题" ? <div contentEditable="true"  suppressContentEditableWarning={true} className='short'  data-key={ite+','+item.id} onInput={(e:any)=>{
                              const index=arr.findIndex((ito:any)=>ito.id===item.id);
                              arr[index].answer=e.target.innerText;
                            
                            }}
                            ></div> : ''
                          }
                          {
                            item.type === "填空题" ? <div contentEditable="true" className='fill' data-key={ite+','+item.id} suppressContentEditableWarning={true}  onInput={(e:any)=>{
                              const index=arr.findIndex((ito:any)=>ito.id===item.id);
                              arr[index].answer=e.target.innerText;
                            
                            }} >{item[ite]}</div> : ''
                          }
                        </div>:<img src={'http://192.168.5.104:7777/'+item[ite]} className="images"></img>;
                    })
                  }
                </div>
              </div>;
            })
          }
          {
            count >=3 ? <Alerts>
              <div className="box">
                <div className="littleBox">
                  <Result
                    status="warning"
                    className='result'
                  />
                  <h3>您已切屏三次，请交卷！</h3>
                  <Button type="primary" onClick={() => {

                    const res:any=document.querySelectorAll('.fill');

                    this.getEndTime();
                  }}>交卷</Button>
                </div>
              </div>
            </Alerts> : ''
          }
        </div>
        <Button type="primary" className='examination' onClick={() => {
          const res:any=document.querySelectorAll('.fill');
          // console.log(res[0]);
          // console.log(res[0].innerHTML);
          // // console.log(res[0].dataset.key);
          // console.log(arr);
          this.getEndTime();
          // this.submitExamList();
        }}>交卷</Button>
      </div>
    );
  }
  getLists=(answers:any)=>{  
    // console.log(answers);
    
    const arry=Object.keys(answers).filter((ita) => {
      if(answers.type === "单选题"){
        return ita.indexOf("option") !== -1;
      }else if(answers.type === "多选题"){
        return ita.indexOf("option") !== -1;
      }else if(answers.type === "判断题"){
        return ita.indexOf("estimate") !== -1;
      }else if(answers.type === "填空题"){
        return ita.indexOf("answer") !== -1;
      }else{
        return ita.indexOf('short') !== -1;
      }
    });    

    

    const arr=JSON.parse(JSON.stringify(arry));
    // console.log(arr);
    
    arry.forEach(item=>{
      answers[item]===null?arr.splice(arr.indexOf(item),1):'';
    });
    // console.log(arr);
    
    return arr;
  };
 
  // 点击获取交卷的时间
  getEndTime = () => {
    const {startTime,examId} = this.props;
    const {ending,arr} = this.state;
    const endTime = new Date().getTime();
    const cha = endTime - startTime;
    // 算小时
    const nowhours = Math.floor((cha % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)) >= 10 ? Math.floor((cha % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)) : (Math.floor((cha % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)) >= 0 ? '0' + Math.floor((cha % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)) : '00');
    // 算分钟
    const nowMin = Math.floor((cha % (1000 * 60 * 60)) / (1000 * 60)) >= 10 ? Math.floor((cha % (1000 * 60 * 60)) / (1000 * 60)) : (Math.floor((cha % (1000 * 60 * 60)) / (1000 * 60)) >= 0 ? '0' + Math.floor((cha % (1000 * 60 * 60)) / (1000 * 60)) : '00');
    // 算秒数
    const nowSen = Math.ceil((cha % (1000 * 60)) / 1000) >= 10 ? Math.ceil((cha % (1000 * 60)) / 1000) : (Math.ceil((cha % (1000 * 60)) / 1000) >= 0 ? '0' + Math.ceil((cha % (1000 * 60)) / 1000) : '00');

    const time = nowhours + ':' + nowMin + ':' + nowSen;
    // console.log(time);
    // this.setState({ending:time});
    
    const student:any=localStorage.getItem('student');
    // console.log(JSON.parse(student));
    const students = JSON.parse(student);
    // console.log(s[0].id);
    
    
    submitExam({id:students[0].id,ending:time,examList:arr,examId}).then((res:any) => {
      console.log(res.code);
      if(res.code === '01'){
        console.log(this.props);
        
        this.props.history.push("/paper");
      }
      
    });
  };
  // 点交卷的时候提交的考试信息
  submitExamList = () => {
    const {ending,arr} = this.state;
    const student:any=localStorage.getItem('student');
    const students = JSON.parse(student);
    submitExam({id:students[0].id,ending,examList:arr}).then(res => {
      // console.log(res);

      
    });
  };
}
// eslint-disable-next-line @typescript-eslint/ban-ts-comment
//@ts-ignore
export default withRouter(Topic);