import React, { Component } from 'react';
import { UserOutlined, TabletOutlined, CloseOutlined } from '@ant-design/icons';
import { Button, Input, Select, Alert, Result, Radio } from 'antd';
import Topic from '../../components/topic/topic';
import AlertTs from '../../components/alert/alert';
import './home.less';
import { reactList, getList, getExamList } from '../../api/index';
import { clearInterval } from 'timers';
// 数据持久化
import { persistStore } from 'redux-persist';
import { PersistGate } from 'redux-persist/lib/integration/react';
// redux
import action from '../../store/action';
import { connect } from 'react-redux';
interface ttr {
  exam_nav?: string,
  name_flag?: string,
  iphone_flag?: string,
  grade_flag?: string,
  sex_flag?: string
}

interface Istate {
  titleData?: Array<ttr>
}
interface Iprops{
 get:any 
}
class Home extends Component {
  state = {
    topic: false,
    subjectValue: [],//学科的value值
    allData: [],//服务器获取到的数据
    // nowHours: "00",//小时
    // nowMinutes: '00',//分钟
    nowSecned: '00:00:00',//秒
    nameValue: '',//姓名input框的value值
    phoneValue: '',//电话号码的value
    Alerts: false,//alert弹框
    count: 0,//切屏的次数
    alertOne: false,//切换页面的次数弹框
    titleData: [{ exam_nav: "", name_flag: 0, iphone_flag: 0, grade_flag: 0, sex_flag: 0 }],//需要填写的个人信息
    sexValue: '',//性别的value值
    examLists: [],//试题的列表
    createTime: '',//开始时间
    endTime: '',//结束时间
    timeAlert: false,//不在时间内弹框
    startTime:'',//点击开始考试获取开始的时间
    examId:'',//考试的id
    zeroTwo:false,//当返回值为02的时候
    zero:''//返回的值
    // zeroThree:false,//当返回值为03的时候
    // zeroFour:false,//当返回值为04的时候
    // zeroFive:false//当返回值为05的时候
  };

  render() {
    const { topic, alertOne, timeAlert,zero, zeroTwo,startTime,examId,examLists, createTime, endTime, sexValue, allData, titleData, nowSecned, nameValue, phoneValue, subjectValue, Alerts, count } = this.state;
    const { Option } = Select;

    return (

      <div className='hide_cover'>
        {/* 顶部的倒计时部分 */}
        <div id="timeCounter">
          <i className="icon_time"></i>
          <div id="submitTimer">总计时 <span>{nowSecned}</span></div>
        </div>
        {/* 下面的答题卡部分 */}
        {/* 大盒子 */}
        <div className="wjContent clear" id='survey_page'>
          {/* 内容盒子 */}
          <div className="content_box content_top">
            {/* web前端面试题 */}
            <div className="content" id='begin_content'>
              <div className="wjtitle mtop project_title">
                <h1>
                  <p>web前端笔试题</p>
                </h1>
              </div>
              <div className="wjintro mtop desc_begin">
                {titleData[0].exam_nav}
              </div>
              {/* 蓝色的线 */}
              <div className="wjhr mtop"></div>
            </div>
            {/* 单选多选题 */}
            <div id="question_box">
              {/* 姓名 */}
              {
                titleData[0].name_flag === 1 ? <div className="wjques maxtop question">
                  {/* 姓名 */}
                  <div className="title">
                    <div className="answer_title">
                      {/* <span className="Qnum">1.</span> */}
                      <span>姓名:</span>
                      <span className="asterisk">*</span>
                    </div>
                  </div>
                  {/* 姓名的input框 */}
                  <div className="matrix">
                    <Input placeholder="请输入您的姓名" prefix={<UserOutlined className="site-form-item-icon" />}
                      onChange={(e) => {
                        // console.log(e.target.value);
                        this.setState({ nameValue: e.target.value });
                      }} value={nameValue} disabled={topic} />
                  </div>

                </div> : ''
              }
              {/* 电话号码 */}
              {
                titleData[0].iphone_flag === 1 ? <div className="wjques maxtop question">
                  <div className="title">
                    <div className="answer_title">
                      {/* <span className="Qnum">2.</span> */}
                      <span>电话号码:</span>
                      <span className="asterisk">*</span>
                    </div>
                  </div>
                  {/* 电话号码的input框 */}
                  <div className="matrix">
                    <Input placeholder="请输入您的手机号" prefix={<TabletOutlined className="site-form-item-icon" />}
                      disabled={topic} onBlur={(e) => {
                        const phneReg = /^1[3456789][0-9]{9}$/;
                        if (phneReg.test(e.target.value)) {
                          // console.log("符合");
                          this.setState({ phoneValue: e.target.value });
                        } else {
                          console.log("不符合");
                        }

                      }} maxLength={11} />
                  </div>
                </div> : ''
              }
              {/* 选择你要答题的类型 */}
              {
                titleData[0].grade_flag === 1 ? <div className="wjques maxtop question">
                  <div className="title">
                    <div className="answer_title">
                      {/* <span className="Qnum">3.</span> */}
                      <span>请填写你的班级:</span>
                      <span className="asterisk">*</span>
                    </div>
                  </div>
                  {/* 选择专业的 */}
                  <div className="matrix">
                    <Input placeholder="请输入您的班级" prefix={<UserOutlined />}
                      onChange={(e) => {
                        // console.log(e.target.value);
                        this.setState({ subjectValue: e.target.value });
                      }} disabled={topic} />
                  </div>
                </div> : ''
              }
              {/* 性别的选择 */}
              {
                titleData[0].sex_flag === 1 ? <div className="wjques maxtop question">
                  <div className="title">
                    <div className="answer_title">
                      {/* <span className="Qnum">3.</span> */}
                      <span>请选择你的性别:</span>
                      <span className="asterisk">*</span>
                    </div>
                  </div>
                  <div className="matrix">
                    <Radio.Group onChange={(e) => {
                      this.setState({ sexValue: e.target.value });
                    }} disabled={topic}>
                      <Radio value={"男"}>男</Radio>
                      <Radio value={"女"}>女</Radio>
                    </Radio.Group>
                  </div>
                </div> : ''
              }
              {/* 不在时间内的弹框 */}
              {
                timeAlert ? <AlertTs>
                  <div className="box">
                    <div className="littleBox">
                      <Result
                        status="warning"
                        className='result'
                      />
                      <h3>您的考试不在时间段内！</h3>
                      <Button type="primary" onClick={() => {
                        this.setState({ timeAlert: false });
                      }}>关闭</Button>
                    </div>
                  </div>
                </AlertTs> : ''
              }
              {/* 当条件不符合时的弹框 */}
              {
                zeroTwo ? <AlertTs>
                  <div className="box">
                    <div className="littleBox">
                      <Result
                        status="warning"
                        className='result'
                      />
                      <h3>{zero}</h3>
                      <Button type="primary" onClick={() => {
                        this.setState({ zeroTwo: false });
                      }}>关闭</Button>
                    </div>
                  </div>
                </AlertTs> : ''
              }
            </div>
            <Button type="primary" className={topic ? 'btns' : 'btn'} onClick={() => {
              // console.log(Alerts);
              // 判断是否符合登录条件
              if ((titleData[0].grade_flag === 0 ? false : subjectValue.length === 0) || 
              (titleData[0].name_flag === 0 ? false : nameValue === '') ||
              ( titleData[0].iphone_flag === 0 ? false : phoneValue === '') || 
               (titleData[0].sex_flag === 0 ? false : sexValue === '')) {
                // 如果不符合就不打开试题
                if(titleData[0].grade_flag === 1 && subjectValue.length === 0){
                  alert("班级不能为空");
                }else if(titleData[0].name_flag === 1 && nameValue === ''){
                  alert("名字不能为空");
                }else if(titleData[0].iphone_flag === 1 && phoneValue === ''){
                  alert("电话不能为空");
                }else if(titleData[0].sex_flag === 1 && sexValue === ''){
                  alert("性别不能为空");
                }
              } else {
                // 符合就打开
                this.getExamLists();
              }
            }}>开始考试</Button>
            {
              // eslint-disable-next-line react/no-string-refs
              // eslint-disable-next-line @typescript-eslint/ban-ts-comment
              //@ts-ignore
              topic ? <Topic count={count} allDatas={allData} startTime={startTime} examId={examId}></Topic> : ''
            }
          </div>
        </div>
        {/* 未填个人信息弹窗 */}
        {
          Alerts ? <div>
            <Alert message="Error" type="error" showIcon className='alert' /><CloseOutlined className='alerts' onClick={() => {
              this.setState({ Alerts: false });
            }} />
          </div> : ''
        }
        {/* 切换页面弹窗 */}
        {
          alertOne && count < 3 ? <AlertTs>
            <div className="box">
              <div className="littleBox">
                <Result
                  status="warning"
                  className='result'
                />
                <h3>您已切屏{count}次，切屏三次就要交卷了！</h3>
                <Button type="primary" onClick={() => {
                  this.setState({ alertOne: false });
                }}>关闭</Button>
              </div>
            </div>
          </AlertTs> : ''
        }
      </div>
    );
  }
  // 获取到学科的数据
  startTest = () => {
    const { topic, subjectValue, Alerts } = this.state;
    reactList({ list: this.state.examLists }).then(res => {
      const { allData } = this.state;
      this.setState({ allData: res.data, topic: true });
    });
  };


  // 给后台发送的数据请求
  getExamLists = () => {
    const {nameValue, phoneValue, subjectValue,examId,zero,zeroTwo} = this.state;
    getExamList({nameValue, phoneValue, subjectValue,examId}).then((res:any) => {
      console.log(res);
      localStorage.setItem('student',JSON.stringify(res.data));
      
      if(res.code === '01'){
        this.setState({ Alerts: false });
        // 开始倒计时
        this.end();
        // 获取到当前的时间
        this.getNowTime();
        // 获取数据
        this.startTest();
        // 全屏
        this.launchFullscreen(document.documentElement);
        // 切屏
        window.addEventListener("focus", this.enter);
        window.addEventListener("blur", this.leave);
        // 看学生是否答过题

      }else if(res.code === '02'){
        // alert("请填写正确的姓名");
        this.setState({zero:res.msg,zeroTwo:true});
      }else if(res.code === '03'){
        // alert("请填写正确的电话号");
        this.setState({zero:res.msg,zeroTwo:true});
      }else if(res.code === '04'){
        // alert("请填写正确的班级");
        this.setState({zero:res.msg,zeroTwo:true});
      }else if(res.code === '05'){
        // alert("该考生已考过该考场考试");
        this.setState({zero:res.msg,zeroTwo:true});
      }
    });
  };

  // 专业类型
  handleChange = (value: any) => {
    const { subjectValue } = this.state;
    this.setState({ subjectValue: value });

  };

  // 点击开始考试开始倒计时
  endTime = (createTime: any, endTime: any) => {
    const {  nowSecned } = this.state;
    const now: any = new Date(createTime).getTime();//取11位时间戳
    const endTimes: any = new Date(endTime).getTime();
    const newDate = new Date().getTime();
    
    
    const cha = endTimes - newDate;
    if (now < newDate < endTimes) {
      
      const nowhours = Math.floor(cha /1000 /60 /60 % 24) >= 10 ? Math.floor(cha /1000 /60 /60 % 24) : (Math.floor(cha /1000 /60 /60 % 24) >= 0 ? '0' + Math.floor(cha /1000 /60 /60 % 24) : '00');
      const nowMin = Math.floor((cha % (1000 * 60 * 60)) / (1000 * 60)) >= 10 ? Math.floor((cha % (1000 * 60 * 60)) / (1000 * 60)) : (Math.floor((cha % (1000 * 60 * 60)) / (1000 * 60)) >= 0 ? '0' + Math.floor((cha % (1000 * 60 * 60)) / (1000 * 60)) : '00');
      const nowSen = Math.ceil((cha % (1000 * 60)) / 1000) >= 10 ? Math.ceil((cha % (1000 * 60)) / 1000) : (Math.ceil((cha % (1000 * 60)) / 1000) >= 0 ? '0' + Math.ceil((cha % (1000 * 60)) / 1000) : '00');
      // console.log(cha);
      // console.log(cha / 1000 / 60 /60/24);
      
      // const time = nowhours+ ':' + nowMin + ':' + nowSen;
      // const nowhour = nowhours >= 0 && nowhours < 10 ? '0' + nowhours : '' + nowhours;
      // const nowM = nowMin >= 0 && nowMin < 10 ? '0' + nowMin : '' + nowMin;
      // const nowS = nowSen >= 0 && nowSen < 10 ? '0' + nowSen : '' + nowSen;
      const time = nowhours+ ':' + nowMin + ':' + nowSen;
      this.setState({ nowSecned: time });
    } else {
      const { timeAlert } = this.state;
      this.setState({ timeAlert: true });
    }
  };

  end = () => {
    setInterval(() => {
      const { createTime, endTime } = this.state;
      this.endTime(createTime, endTime);
    }, 1000);
  };

  myTimer = () => {
    const d = new Date();
    const t = d.toLocaleTimeString();
  };

  // 记录考试切屏次数
  leave = () => {
    // let count = 0;
    const { count, alertOne } = this.state;
    // count += 1;
    this.setState({ count: count + 1, alertOne: true });
  };
  enter = () => {
    const setTimer = setInterval(() => {
      this.myTimer();
    }, 1000);
  };
  // 全屏显示
  launchFullscreen = (e: any) => {
    if (e.requestFullscreen) {
      e.requestFullscreen();
    } else if (e.mozRequestFullScreen) {
      e.mozRequestFullScreen();
    } else if (e.webkitRequestFullscreen) {
      e.webkitRequestFullscreen();

    } else if (e.msRequestFullscreen) {
      e.msRequestFullscreen();
    }
  };

  // 一进页面获取到的数据
  componentDidMount() {
    // eslint-disable-next-line react/no-string-refs
    console.log(this.refs);
    console.log(this);
    
    
    // console.log(115315159 / 1000/60/60/24);
    const url = window.location.href;
    console.log(url);
    
    const urls = this.getCaption(url, "?");
    const id = urls.slice(10);
    getList({ id }).then(res => {
      const { titleData, examLists, createTime, endTime ,examId} = this.state;
      // console.log(res.data[0].exam_questions );
      // examLists.push(res.data[0].exam_questions);
      console.log(res);
      
      this.setState({ titleData: res.data, examLists: res.data[0].exam_questions, createTime: res.data[0].create_time, endTime: res.data[0].end_time,examId:res.data[0].id });
      // console.log(res.data);

    });
  }

  // 获取地址栏的地址
  getCaption = (url: any, parameter: any) => {
    const index = url.lastIndexOf(parameter);
    url = url.substring(index + 1, url.length);
    return url;
  };


  // 开始计时
  getNowTime = () => {
    const {startTime} = this.state;
    const nowTime = new Date().getTime();
    // console.log(nowTime);

    // eslint-disable-next-line react/prop-types
    // this.props.get(nowTime);
    this.setState({startTime:nowTime});
  };
}
export default connect(state => state, action)(Home);